import { Link,useLocation,useNavigate } from 'react-router-dom'
import { auth } from '../Auth/Auth'
import { signOut } from "firebase/auth";
import styles from './Header.module.css'
import logo from '../../images/logo.png'
import { Hamburger } from './hamburger/hamburger';

export const isSmartPhone=()=> {
    if (navigator.userAgent.match(/iPhone|Android.+Mobile/)) {
      return true;
    } else {
      return false;
    }
  }

export const HeaderTop= () =>{
    const navigate = useNavigate()
    const pathname = useLocation().pathname;
    const now = pathname.replace('/','')
    if (isSmartPhone()){
        return(
            <div className={styles.body}>
                <div className={styles.flex}>
                    <img src={logo} className={styles.Plogo} alt='' onClick={() =>{navigate('/Home')}}/>
                    <div className={styles.Stitle}>
                    <p1 className={styles.Stitle}>Scheduler - {now}</p1>
                    </div>
                </div>
                <hr />
        </div>
        )
    }else{

        return(
            <div className={styles.body}>
                <div className={styles.flex}>
                    <img src={logo} className={styles.Plogo} alt='' onClick={() =>{navigate('/Home')}}/>
                    <div className={styles.title}>
                    <p1 className={styles.title}>Scheduler - {now}</p1>
                    </div>
                </div>
                <hr />
        </div>
    )
}
}

export const Header = () => {
    const navigate = useNavigate()
    const pathname = useLocation().pathname;
    const now = pathname.replace('/','')
    
    if (isSmartPhone()){
        return(
            <div className={styles.body}>
                <div className={styles.flex}>
                    <img src={logo} className={styles.Slogo} alt='' onClick={() =>{navigate('/Home')}}/>
                    <div className={styles.Stitle}>
                    <p1 className={styles.Stitle}>Scheduler - {now}</p1>
                    </div>
                </div>
                <Hamburger />
                <hr />
            </div>
        )
    }else{
        return(

            <div className={styles.body}>
                <div className={styles.flex}>
                    <img src={logo} className={styles.Plogo} alt='' onClick={() =>{navigate('/Home')}}/>
                    <div className={styles.title}>
                    <p1 className={styles.title}>Scheduler - {now}</p1>
                    </div>
                </div>
                <div align="right">
                    <Link onClick={()=>{signOut(auth)}} to = '/'  className={styles.logout}>Logout</Link>
                </div>
                    
                <div align="left">
                    <Link to = '/Home' className={styles.home}>Home</Link>
                    <Link to = '/Assignments' className={styles.assignments}>Assignments</Link>
                    <Link to = '/Classes' className={styles.classes}>Classes</Link>
                    <a href="https://mail.google.com/chat/u/0/#chat/home" class={styles.chats} target="_blank" rel="noopener noreferrer">Chats</a>
                    <Link to = '/Settings' className={styles.settings}>Settings</Link>
                </div>
                <hr />
            </div>
        )
    }
   
}