import styles from './UpdateLog.module.css'
import { Link } from 'react-router-dom'
import { HeaderTop } from '../Header/Header'

export const UpdateLog =()=>{
    return(
        <div>
        <HeaderTop />
        <div className={styles.body}>
        <h1 className={styles.title}>Scheduler Update Log</h1>
        <h3 className={styles.virsion}>v0.0.1 - 2024/5/</h3>
        <p className={styles.info}>
            First release. <br />
            ＜現状＞<br />
            ・1週間分の時間割表示<br />
            ＜課題＞<br />
            ・データ受取の自動化<br />
            ・同ユーザ異デバイス間での設定の同期<br />
            ・2週間分の時間割の表示<br />
            ＜展望＞<br />
            ・時間割テーブルの配置自由化<br />
            ・Homeにて付箋の貼り付け機能<br />
            ・教科カラー設定の自由化<br />
        </p>

        <Link to='/' className={styles.f_form}>Return to Home</Link>
        <a href="https://forms.gle/9ExuRPUDyRMcMxxp8" className={styles.f_form} target="_blank" rel="noopener noreferrer">Add Feedback</a>
        </div>
        </div>
    )
}