import { Header } from "../Header/Header"
import styles from "./Settings.module.css"
import Select from "react-select"
import { auth } from "../Auth/Auth"
import { useEffect,useState } from "react"
import { useNavigate } from "react-router-dom"

export const Settings = () =>{
    const humanity2 =["2-1","2-2","2年国探"]
    const science2 =["2-3","2-4","2年理探"]
    const humanity3 = ["3-1","3-2","3年国探"]
    const science3 =["3-3","3-4","3年理探"]
    const sciencetankyuu = ["2年理探","3年理探"]

    const [classes,setclasses] =useState(localStorage.getItem("class")?localStorage.getItem('class'):'unselected')
    const [art,setart] =useState(localStorage.getItem("art")?localStorage.getItem('art'):'unselected')
    const [social,setsocial] =useState(localStorage.getItem("social")?localStorage.getItem('social'):'unselected')
    const [science,setscience] =useState(localStorage.getItem("science")?localStorage.getItem('science'):'unselected')
    const [politic,setpolitic] =useState(localStorage.getItem("politic")?localStorage.getItem('politic'):'unselected')
    const classlist =[
        { value:"1-1",label:"1-1"},
        { value:"1-2",label:"1-2"},
        { value:"1-3",label:"1-3"},
        { value:"1-4",label:"1-4"},
        { value:"1-5",label:"1-5"},
        { value:"1-6",label:"1-6"},
        { value:"2-1",label:"2-1"},
        { value:"2-2",label:"2-2"},
        { value:"2-3",label:"2-3"},
        { value:"2-4",label:"2-4"},
        { value:"2年理探",label:"2年理探"},
        { value:"2年国探",label:"2年国探"},
        { value:"3-1",label:"3-1"},
        { value:"3-2",label:"3-2"},
        { value:"3-3",label:"3-3"},
        { value:"3-4",label:"3-4"},
        { value:"3年理探",label:"3年理探"},
        { value:"3年国探",label:"3年国探"},
    ]

    const arts = [
        { value:" 音楽",label:" 音楽"},
        { value:" 美術",label:" 美術"},
        { value:" 書道",label:" 書道"}
    ]

    const politics =[   
        {value:" 世史",label:" 世界史"},
        {value:" 政治",label:" 政治"},
        {value:" 経済",label:" 経済"}
    ]

    const socials = humanity3.includes(classes) ?
    [   { value:" 地理β",label:" 地理β"},
        { value:" 歴史β",label:" 歴史β"}
    ]:[ { value:" 地理",label:" 地理"},
        { value:" 日史",label:" 日本史"}
    ]

    const sciences = sciencetankyuu.includes(classes)?
    [   {value:" 理物",label:" 理数物理"},
        {value:" 理生",label:" 理数生物"}
    ]:[ {value:" 物理",label:" 物理"},
        {value:" 生物",label:" 生物"}
    ]

    const ClassChange = selectedOption => {
        localStorage.clear()
        setart('unselected')
        setsocial('unselected')
        setscience('unselected')
        setpolitic('unselected')
        localStorage.setItem("class",selectedOption.value)
        setclasses(selectedOption.value)
    };

    const ArtChange = selectedOption => {
        localStorage.setItem("art",selectedOption.value)
        setart(selectedOption.value)
    };

    const SocialChange = selectedOption => {
        localStorage.setItem("social",selectedOption.value)
        setsocial(selectedOption.value)
    };
    const ScienceChange = selectedOption => {
        localStorage.setItem("science",selectedOption.value)
        setscience(selectedOption.value)
    };
    const PoliticChange = selectedOption => {
        localStorage.setItem("politic",selectedOption.value)
        setpolitic(selectedOption.value)
    };

    const selectStyle = {
        control: () => ({
          width: 135,
          height:25,
          border: "0.5px solid gray",
          display: "flex",
        }),
      };

    const navigate = useNavigate()

    const ElectiveSubjects = () =>{
        if (humanity2.includes(classes)){
            return(
                <div>
                    <div>
                        <p className={styles.content}>芸術: {art}</p><br/>
                        <Select options={arts} components={{IndicatorSeparator: () => null,}} onChange={ArtChange} styles={selectStyle} className={styles.selection}/><br/>
                        <p className={styles.content}>日本史/地理:{social}</p><br/>
                        <Select options={socials} components={{IndicatorSeparator: () => null,}} onChange={SocialChange}  styles={selectStyle} className={styles.selection}/>
                    </div>
                </div>
            )
        }else if (science2.includes(classes)){
            return(
                <div>
                    <div>
                        <p className={styles.content}>芸術: {art}</p><br />
                        <Select options={arts} components={{IndicatorSeparator: () => null,}} onChange={ArtChange}  styles={selectStyle} className={styles.selection}/><br/>
                        <p className={styles.content}>日本史/地理: {social}</p><br />
                        <Select options={socials} components={{IndicatorSeparator: () => null,}} onChange={SocialChange} styles={selectStyle} className={styles.selection}/><br/>
                        <p className={styles.content}>物理/生物: {science}</p><br/>
                        <Select options={sciences} components={{IndicatorSeparator: () => null,}} onChange={ScienceChange} styles={selectStyle} className={styles.selection}/>
                    </div>
                </div>
            )
        }else if (humanity3.includes(classes)){
            return(
                <div>
                    <div>
                        <p className={styles.content}>日本史/地理: {social}</p><br/>
                        <Select options={socials} components={{IndicatorSeparator: () => null,}} onChange={SocialChange} styles={selectStyle} className={styles.selection}/><br/>
                        <p className={styles.content}>世政経: {politic}</p><br/>
                        <Select options={politics} components={{IndicatorSeparator: () => null,}} onChange={PoliticChange} styles={selectStyle} className={styles.selection}/>
                    </div>
                </div>
            )
        }else if (science3.includes(classes)){
            return(
                <div>
                    <div>
                        <p className={styles.content}>日本史/地理: {social}</p><br/>
                        <Select options={socials} components={{IndicatorSeparator: () => null,}} onChange={SocialChange} styles={selectStyle} className={styles.selection}/><br/>
                        <p className={styles.content}>物理/生物: {science}</p><br/>
                        <Select options={sciences} components={{IndicatorSeparator: () => null,}} onChange={ScienceChange} styles={selectStyle} className={styles.selection}/>
                    </div>
                </div>
            )
        }else{
            return(
                <div>
                    <p className={styles.content}>選択科目なし</p>
                </div>
            )
        }   
    }

    useEffect(()=>{
    if (!(auth.currentUser)){
        navigate("/",{state:"/Settings"})
    }})

    if(auth.currentUser){
        return(
            <div>
                <Header />
                <div className={styles.body}>
                    <h1 className={styles.greeting}>Hello, {auth.currentUser.displayName}</h1> <br />
                    <div className={styles.info}>
                        <h1 className={styles.margin}>Your Account</h1> <br />
                        <h2 className={styles.content}>{auth.currentUser.email}</h2><br />
                        <h1 className={styles.margin}>Class</h1><br />
                        <h2 className={styles.content}>{classes}</h2>
                        <Select options={classlist} onChange={ClassChange} styles={selectStyle} className={styles.selection}/><br />
                        <h1 className={styles.margin}>Courses</h1>
                        <ElectiveSubjects />
                    </div>
                </div>
            </div>
        )
    }
}
